<template>
  <div>
  <ValidationObserver v-slot="v" ref="form">
    <VNavigationDrawer
      class="registration-drawer"
      hide-overlay
      right
      fixed
      width="100%"
      v-model="showRegistrationDrawer"
    >
      <VBtn icon @click="cancelAction">
        <span class="betfont icon-close"></span>
      </VBtn>
      <VContainer fluid>
        <VRow>
          <VCol cols="12" class="d-flex justify-center">
            <div class="brand-logo no-resize" :class="`${setlangCode}`"></div>
          </VCol>

          <VCol cols="12">
            <VStepper v-model="e1" class="elevation-0">
              <VStepperItems>
                <VStepperContent step="1">
                  <h2 class="mb-3">{{$t(`sign.up`)}}</h2>
                <ValidationProvider
                  name="username"
                  :rules="{                                 
                    usernameAlphaNum: true,
                    usernameMinMax: { min: 5, max: 15 },
                    validUserName: true,
                  }"              
                  v-slot="{ invalid,errors }"
                >                  
                  <VTextField
                    class="mb-4"
                    :label="$t(`username`)"
                    hide-details="auto"
                    filled
                    maxlength="15"
                    minlength="5"
                    v-model="profileModel.Username"
                  />     
                  <span v-if="invalid" style="color:red;">{{ errors[0] }}</span>                  
                  </ValidationProvider>   
                  <ValidationProvider
                  name="usernamereq"
                  rules="required"
                  v-slot="{ invalid }"
                  >                           
                  <VTextField v-model="profileModel.Username" class="d-none"/>
                    <span v-if="invalid" style="color:red;" >{{ $t(`jquery.validator.required`) }}</span>
                  </ValidationProvider>                    
                  <ValidationProvider
                      name="newPassword"
                      :rules="{                       
                        passwordAlphaNum: true,                  
                        passwordMinMax: { min: 6, max: 15 }
                      }"   
                      v-slot="{ invalid,errors }"
                  >                               
                  <VTextField
                    class="mb-4"
                    :label="$t(`password`)"
                    hide-details="auto"
                    filled
                    :type="showPass ? 'text' : 'password'"
                    :append-icon="
                      showPass ? 'icon-show-password' : 'icon-hide-password'
                    "
                    maxlength="15"
                    minlength="6"
                    v-model="profileModel.Password"
                    @click:append="showPass = !showPass"                    
                  />                  
                  <span v-if="invalid" style="color:red;">{{ errors[0] }}</span>      
                  </ValidationProvider> 
                  <ValidationProvider
                  name="newPasswordreq"
                  rules="required"
                  v-slot="{ invalid }"
                  >                           
                  <VTextField v-model="profileModel.Password" class="d-none"/>
                    <span v-if="invalid" style="color:red;" >{{ $t(`jquery.validator.required`) }}</span>
                  </ValidationProvider>                    
                  <ValidationProvider
                    name="ConfirmPassword"
                    rules="validatePassword"
                    v-slot="{ invalid, errors }"
                  >                                   
                  <VTextField
                    class="mb-4"
                    :label="$t(`confirm.new.password`)"
                    hide-details="auto"
                    filled
                    :type="showPass2 ? 'text' : 'password'"
                    :append-icon="
                      showPass2 ? 'icon-show-password' : 'icon-hide-password'
                    "
                    v-model="profileModel.ConfirmPassword"
                    @click:append="showPass2 = !showPass2"
                  />   
                  <span v-if="invalid" style="color:red;">{{ errors[0] }}</span>                              
                </ValidationProvider> 
                <ValidationProvider
                  name="ConfirmPasswordreq"
                  rules="required"
                  v-slot="{ invalid }"
                >                           
                <VTextField v-model="profileModel.ConfirmPassword" class="d-none"/>
                  <span v-if="invalid" style="color:red;" >{{ $t(`jquery.validator.required`) }}</span>
                </ValidationProvider>                              
                </VStepperContent>
                <VStepperContent class="pb-1" step="2">
                  <h2 class="mb-3">{{$t(`general.account.info`)}}</h2>
                  <ValidationProvider
                      name="firstname"
                      rules="required"
                      v-slot="{ invalid }"
                  >                     
                  <VTextField
                    class="mb-4"
                    :label="$t(`first.name`)"
                    hide-details="auto"
                    filled
                    v-model="profileModel.FirstName"
                  />
                  <span v-if="invalid" style="color:red;">{{ $t(`jquery.validator.required`) }}</span>   
                  </ValidationProvider>  
                  <ValidationProvider
                      name="lastname"
                      rules="required"
                      v-slot="{ invalid }"
                  >                                     
                  <VTextField
                    class="mb-4"
                    :label="$t(`last.name`)"
                    hide-details="auto"
                    filled
                    v-model="profileModel.LastName"
                  />
                  <span v-if="invalid" style="color:red;">{{ $t(`jquery.validator.required`) }}</span>  
                  </ValidationProvider>                  
                  <h3 class="text-xs text--secondary mb-2">
                    {{$t(`preferred.language.and.currency`)}}
                  </h3>
                  <div class="d-flex">
                    <div class="mr-1" style="width:60%">

                      <VTextField
                        class="mb-4"
                        :label="$t(`preferred.language`)"
                        hide-details="auto"
                        readonly
                        append-icon="icon-arrowhead-down"
                        filled    
                        :value="getLanguageText(profileModel.Language)"   
                        @click.stop="selectPayload('lang')"                        
                      />    
                      <ValidationProvider
                      name="language"
                      rules="required"
                      v-slot="{ invalid }"
                      >                           
                      <VTextField v-model="profileModel.Language" class="d-none"/>
                        <span v-if="invalid" style="color:red;" >{{ $t(`jquery.validator.required`) }}</span>
                      </ValidationProvider>                      
                    </div>
                    <div class="ml-1" style="width:40%">
                      <VTextField
                        class="mb-4"
                        :label="$t(`currency`)"
                        hide-details="auto"
                        readonly
                        append-icon="icon-arrowhead-down"
                        filled
                        :value="getCurrencyText(profileModel.Currency)"   
                        @click.stop="selectPayload('Currency')"                            
                      />
                      <ValidationProvider
                      name="currency"
                      rules="required"
                      v-slot="{ invalid }"
                      >                           
                      <VTextField v-model="profileModel.Currency" class="d-none"/>
                        <span v-if="invalid" style="color:red;" >{{ $t(`jquery.validator.required`) }}</span>
                      </ValidationProvider>                        
                    </div>
                  </div>
                  <VTextField
                    class="mb-4"
                    label="Affiliate ID (Optional)"
                    hide-details="auto"
                    filled
                    v-model="profileModel.AffiliateCode"
                  />
                </VStepperContent>

                <VStepperContent step="3">
                  <h2 class="mb-3">{{$t(`contact.info`)}}</h2>
                  <div class="d-flex">
                    <div class="mr-1" style="width:46%">
                      <VTextField
                        class="mb-4"
                        :label="$t(`area.code`)"
                        hide-details="auto"
                        append-icon="icon-arrowhead-down"
                        readonly
                        filled
                        :value="getDialCodeText(profileModel.DialCode)"
                        @click="selectPayload('DialCode')"  
                      />
                      <ValidationProvider
                        name="DialCode"
                        rules="required"
                        v-slot="{ invalid }"
                      >
                        <VTextField v-model="profileModel.DialCode" class="d-none" />
                        <span v-if="invalid" style="color:red;" >{{ $t(`jquery.validator.required`) }}</span>
                      </ValidationProvider>                        
                    </div>
                    <div class="ml-1" style="width:54%">
                      <ValidationProvider
                        name="ContactNo"
                        rules="numericOnly"
                        v-slot="{ invalid,errors }"
                      >                      
                      <VTextField
                        type="numeric"
                        inputmode="numeric"
                        class="mb-4"
                        :label="$t(`contact.number`)"
                        hide-details="auto"
                        filled                     
                        v-model="profileModel.ContactNo"
                      />                         
                      <span v-if="invalid" style="color:red;">{{ errors[0] }}</span>     
                      </ValidationProvider>   
                      <ValidationProvider
                        name="ContactNoreq"
                        rules="required"
                        v-slot="{ invalid }"
                      >                           
                      <VTextField v-model="profileModel.ContactNo" class="d-none"/>
                        <span v-if="invalid" style="color:red;" >{{ $t(`jquery.validator.required`) }}</span>
                      </ValidationProvider>                                             
                    </div>
                  </div>
                  <ValidationProvider
                    name="email"
                    rules="validEmail|email"
                    v-slot="{ invalid,errors }"
                  >                   
                  <VTextField
                    class="mb-4"
                    :label="$t(`email`)"
                    hide-details="auto"
                    filled
                    v-model="profileModel.Email"                    
                  />                            
                  <span v-if="invalid" style="color:red;">{{ errors[0] }}</span>  
                  </ValidationProvider>  
                  <ValidationProvider
                    name="emailreq"
                    rules="required"
                    v-slot="{ invalid }"
                  >                           
                  <VTextField v-model="profileModel.Email" class="d-none"/>
                    <span v-if="invalid" style="color:red;" >{{ $t(`jquery.validator.required`) }}</span>
                  </ValidationProvider>                     
                  <h3 class="text-xs text--secondary mb-2">
                    {{$t(`other.communication.channel`)}}
                  </h3>
                  <div class="d-flex">
                    <div class="mr-1" style="width:40%">
                      <VTextField
                        class="mb-4"
                        :label="$t(`channel`)"
                        hide-details="auto"
                        append-icon="icon-arrowhead-down"
                        filled
                        :value="getChatToolText(profileModel.ChatTool)"
                        @click="selectPayload('ChatTool')"                         
                      />                        
                    </div>
                    <div class="ml-1" style="width:60%">                   
                      <VTextField
                        class="mb-4"
                        :label="$t(`chat.tool.address`)"
                        hide-details="auto"
                        filled
                        v-model="profileModel.ChatAddress"
                      />    
                    </div>
                  </div>
                </VStepperContent>
                <VStepperContent step="4">
                  <h2 class="mb-3">{{$t(`location.info`)}}</h2>
                  <ValidationProvider
                    name="Address"
                    rules="required"
                    v-slot="{ invalid }"
                  >                    
                  <VTextarea
                    class="mb-4"
                    :label="$t(`address`)"
                    hide-details="auto"   
                    rows="2"               
                    filled
                    no-resize
                    auto-grow
                    v-model="profileModel.Address"
                  />
                  <span v-if="invalid" style="color:red;">{{ $t(`jquery.validator.required`) }}</span>    
                  </ValidationProvider>  
                  <ValidationProvider
                    name="city"
                    rules="required"
                    v-slot="{ invalid }"
                  >                                       
                  <VTextField
                    class="mb-4"
                    :label="$t(`city.province`)"
                    hide-details="auto"
                    filled
                    v-model="profileModel.City"
                  />
                  <span v-if="invalid" style="color:red;">{{ $t(`jquery.validator.required`) }}</span>    
                  </ValidationProvider>                    
                  <div class="d-flex">
                    <div class="mr-1" style="width:40%">
                      <VTextField
                        class="mb-4"
                       :label="$t(`country`)"
                        hide-details="auto"
                        append-icon="icon-arrowhead-down"
                        filled
                       :value="getCountryText(profileModel.Country)"    
                        @click="selectPayload('Country')"                      
                      />
                      <ValidationProvider
                        name="Country"
                        rules="required"
                        v-slot="{ invalid }"
                      >
                        <VTextField v-model="profileModel.Country" class="d-none" />
                        <span v-if="invalid" style="color:red;" >{{ $t(`jquery.validator.required`) }}</span>
                      </ValidationProvider>                       
                    </div>
                    <div class="ml-1" style="width:60%">
                      <ValidationProvider
                        name="city"
                        rules="required"
                        v-slot="{ invalid }"
                      >                          
                      <VTextField
                        class="mb-4"
                        :label="$t(`postal.code.zip`)"
                        hide-details="auto"
                        filled
                        v-model="profileModel.Postal"
                      />
                     <span v-if="invalid" style="color:red;">{{ $t(`jquery.validator.required`) }}</span>  
                      </ValidationProvider>                        
                    </div>
                  </div>
                </VStepperContent>
                <VStepperContent step="5">
                  <h2 class="mb-3">{{$t(`website.info`)}}</h2>
                  <VCard color="#fbfbfb" flat>
                    <VExpansionPanels color="transparent" flat v-model="profileModel.OwnWebsite">
                      <VExpansionPanel class="ma-0 pa-0 elevation-0">
                        <VExpansionPanelHeader color="transparent">
                          <VSpacer>{{$t(`i.have.website`)}}</VSpacer>
                          <span
                            class="betfont text-right text-md"
                            :class="
                              profileModel.OwnWebsite === 0
                                ? 'icon-radio-on color-orange'
                                : 'icon-radio-off'
                            "
                          ></span>
                        </VExpansionPanelHeader>
                        <VExpansionPanelContent>                         
                          <VTextField
                            class="mb-4"
                            :label="$t(`website.url`)"
                            hide-details="auto"
                            filled
                            v-model="profileModel.WebUrl"  
                            @blur="onWebUrlBlur"                          
                          />
                         <span v-if="isValidateWebUrl"  style="color:red;">{{ $t(`jquery.validator.required`) }}</span>                                                   
                        </VExpansionPanelContent>
                      </VExpansionPanel>
                      <VDivider />
                      <VExpansionPanel class="ma-0 pa-0 elevation-0">
                        <VExpansionPanelHeader color="transparent">
                          <VSpacer>{{$t(`i.dont.have.my. own.website`)}}</VSpacer>
                          <span
                            class="betfont text-right text-md"
                            :class="
                              profileModel.OwnWebsite === 1
                                ? 'icon-radio-on color-orange'
                                : 'icon-radio-off'
                            "
                          ></span>
                        </VExpansionPanelHeader>
                        <VExpansionPanelContent
                          class="elevation-0"
                          color="transparent"
                        >
                          <VTextarea
                            class="mb-4 wrapped-label"
                            label="Describe how you'll market our brand"
                            hide-details="auto"
                            rows="2"
                            filled
                            no-resize
                            auto-grow
                            @blur="onDescribeBlur"  
                            v-model="profileModel.Describe"  
                          />                        
                          <span v-if="isValidateWebUrl"  style="color:red;">{{ $t(`jquery.validator.required`) }}</span>                             
                        </VExpansionPanelContent>
                      </VExpansionPanel>
                    </VExpansionPanels>
                  </VCard>
                </VStepperContent>
                <VStepperContent step="6">
                  <h2 class="mb-3">{{$t(`terms`)}}</h2>
                  <VCard
                    color="#f1f1f1"
                    class="rounded-lg px-3 py-1 align-start"
                    flat
                  >
                    <VCheckbox
                      v-model="checkbox"
                      class="mt-3 align-start"
                      on-icon="icon-checked color-orange"
                      off-icon="icon-unchecked "
                    >
                      <template v-slot:label>                 
                        <div>
                          {{$t(`i.have.read.and.accepted.the`)}}
                          <a
                            href="#"
                            @click.stop="alert()"
                            class="text-decoration-none blue--text"
                            >{{$t(`term.and.condition`)}}</a
                          >
                        </div>
                      </template>
                    </VCheckbox>
                  </VCard>
                  <VCard color="#fbfbfb" class="mt-3 pa-3" flat>
                    <div class="text-center text-sm mb-3">
                      {{$t(`pls.type.captcha`)}}
                    </div>
                    <div class="d-flex justify-center mb-4">
                      <VCard flat outlined>
                        <VImg width="154" height="40" :src="imgSrc"></VImg>                        
                      </VCard>
                      <VBtn
                        depressed
                        outlined
                        class="faint-outline px-0 ml-3"
                        min-width="2.5rem"
                        height="2.5rem"
                        @click="refresh"
                        ><span class="betfont icon-refresh-2"></span
                      ></VBtn>
                    </div>
                    <ValidationProvider
                    name="Captcha"
                    rules="validCaptcha|required"
                     v-slot="{ invalid,errors }"   
                    >                     
                    <VTextField
                      class="mb-4"
                      :label="$t(`captcha.code`)"
                      hide-details="auto"
                      filled
                      maxlength="4"
                      v-model="profileModel.Captcha"
                    />
                     <span v-if="invalid&&errors[0] != 'Captcha is not valid.'" style="color:red;">{{ errors[0] }}</span>
                     <span v-if="invalid&&errors[0]=== 'Captcha is not valid.'" style="color:red;">{{ $t(`jquery.validator.required`) }}</span>
                    </ValidationProvider>
                  </VCard>
                </VStepperContent>
              </VStepperItems>
              <div class="d-flex align-center px-6">
                <VBtn
                  rounded
                  depressed
                  color="primary"
                  min-width="2.32rem"
                  width="1rem"
                  class="px-0"
                  @click="e1 > 1 ? (e1 = e1 - 1) : null"
                  :disabled="e1 == 1"
                >
                <span class="betfont icon-stick-arrow-left"></span>
                </VBtn> 
                <VSpacer>
                  <VStepperHeader v-if="e1 != 6" class="mx-9 elevation-0">
                    <VStepperStep
                      class="justify-center"
                      :complete="e1 > 0"
                      step=""                     
                    >                   
                    </VStepperStep>
                    <VStepperStep
                      class="justify-center"
                      :complete="e1 > 1"
                      step=""
                    >
                    </VStepperStep>
                    <VStepperStep
                      class="justify-center"
                      :complete="e1 > 2"
                      step=""
                    >
                    </VStepperStep>
                    <VStepperStep
                      class="justify-center"
                      :complete="e1 > 3"
                      step=""
                    >
                    </VStepperStep>
                    <VStepperStep
                      class="justify-center"
                      :complete="e1 > 4"
                      step=""
                    >
                    </VStepperStep>
                    <VStepperStep
                      class="justify-center"
                      :complete="e1 > 5"
                      step=""
                    >
                    </VStepperStep>
                  </VStepperHeader>
                </VSpacer>

                <VBtn
                  rounded
                  depressed
                  color="primary"
                  min-width="2.32rem"
                  width="1rem"
                  class="px-0"
                  @click="e1 < 6 ? (e1 = e1 + 1) : null"
                  v-if="e1 != 6"               
                >
                  <span class="betfont icon-stick-arrow-right"></span>
                </VBtn>              
                <VBtn v-if="e1 === 6" depressed color="primary" v-show="checkbox === true && isValidateWebUrl === false" :disabled="v.invalid" @click.stop="confirmAction">{{$t(`submit`)}}</VBtn>
              </div>
            </VStepper>
          </VCol>
        </VRow>
      </VContainer>
    </VNavigationDrawer>
  </ValidationObserver>
  </div>
</template>
<script>
import { VBtn, VRow, VCol, VCard, VCardText, VCardSubtitle } from 'vuetify/lib';
import { mapGetters, mapState, mapActions } from 'vuex';
import { required, email, alpha_num, numeric } from 'vee-validate/dist/rules';
import { ValidationProvider, ValidationObserver, extend } from 'vee-validate';
import minMax from '@/rules/minMax';
export default {
  data: () => ({
    e1: 1,
    showPass: false,
    showPass2: false,
    panel: [],
    checkbox: false,
    selectType: '',
    optionsPayload: [],  
    panel: [],
    isValidateWebUrl:false,
    imgPath: '/Captcha.ashx',   
    versionNumber: new Date().getTime(),
    PlatformId:1    
  }),
  components: {
    VBtn,
    VRow,
    VCol,
    VCard,
    VCardText,
    VCardSubtitle,
    ValidationProvider,
    ValidationObserver    
  },
  props: { 
    showRegistrationDrawer: { required: true, default: false },
    setlangCode:{ default: '' },
    profileModel: {
      type: Object,
      default: () => ({})
    },     
  },
  computed: {
    ...mapState('Auth', ['AuthModel']),
  },
  mounted() {
    extend('required', { ...required });  
    extend('validatePassword', (value) => {
      if (value === this.profileModel.Password) {
        return true;
      }
      return this.$t(`confirm.password.not.validate`);
    });          
    extend('validUserName', (value) => {    
      if (value.length >= 5 && value.length <= 15) {      
        this.CheckUserName(value); 
        if(this.AvailableUserName.data.Status === 1)
        {
           return true; 
        }
        else
        {
          return this.$t(`register.help.invalidUserName`)  
        }
      }   
    });      
    extend('validEmail', (value) => {    
      this.CheckEmail(value);
      if(this.AvailableEmail.data.Status === 0)
      {
          return true; 
      }
      else
      {
        return this.$t(`not.available`)  
      }     
    });       
    extend('usernameMinMax', {
      ...minMax,
      message: this.$t('helpJoinUsUserIdRequire')
    });    
    extend('passwordMinMax', {
      ...minMax,
      message: this.$t('helpJoinUsPassword')
    });      
    extend('email', {
      ...email,
      message: this.$t(`jquery.validator.email`)
    });  
    extend('passwordAlphaNum', {
      ...alpha_num,
      message: this.$t('helpJoinUsPassword')
    });
    extend('usernameAlphaNum', {
      ...alpha_num,
      message: this.$t('helpJoinUsUserIdRequire')
    });  
    extend('numericOnly', {
      ...numeric,
      message: this.$t('numeric.characters.only')
    });   
    extend('validateWebUrlLink', {
      validate(value) {
        var pattern = new RegExp('^(https?:\\/\\/)?' + 
          '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|' + 
          '((\\d{1,3}\\.){3}\\d{1,3}))' + 
          '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*' + 
          '(\\?[;&a-z\\d%_.~+=-]*)?' + 
          '(\\#[-a-z\\d_]*)?$', 'i'); 
        return pattern.test(value);
      },
      message: 'Please enter a valid link.'
    });  
    extend('minmax', {
      validate(value, { min, max }) {
        return value.length >= min && value.length <= max;
      },
      params: ['min', 'max']
    });   
    extend('validCaptcha', (value) => {         
      this.CheckCaptcha(value);     
        if(this.AvailableCaptcha.data.Status === 0)
        {
            return true; 
        }
        else
        {
          return this.$t(`error.verify.captcha`)  
        }      
    });        
  },
  created:function(){  
    this.getLanguage(1);
    this.getCountry(this.langCode);
    this.getChatTools();
    this.getDialCode();   
    this.getCurrencys();
  },
  computed: { 
    ...mapGetters({
      langCode: 'langCode',
    }),   
    ...mapState("Register", ['AvailableUserName','AvailableEmail','RegisterResponse','AvailableCaptcha']),
    ...mapState("Common", ["Languages",'Countrys','ChatTools','DialCodes','Currencys']),
    imgSrc() {
      return `${this.imgPath}?v=${this.versionNumber}`;
    }      
  },  
  methods: {
    ...mapActions({
      openDialog: 'DialogQueue/queue',
      closeDialog: 'DialogQueue/close',
    }),    
    ...mapActions('Register',['registerAccount','CheckUserName','CheckEmail','CheckCaptcha']),
    ...mapActions("Common", ["getLanguage",'getCountry','getChatTools','getDialCode','getCurrencys']),
    confirmAction: function(event) {
      let payload = {
        title: this.$t(`you.want.to.submit`),
        msg: this.$t(`clicking.submit.will.confirm.that`),
        buttons: [
          {
            text: this.$t(`not.yet`),
            color: 'blue--text',
            action: () => {
              this.closeDialog();
            },
          },
          {
            text: this.$t(`submit`),
            color: 'blue--text',
            action: () => {
              this.onRegister();             
            },
          },
        ],
      };
      this.openDialog({
        payload,
        setting: { maxWidth: '18.5rem', closeOnClickOutside: true },
      });
    },
    cancelAction: function(event) {
      let payload = {
        title: this.$t(`leave.this.page`),
        msg: this.$t(`If.you.leave.now`),
        buttons: [
          {
            text: this.$t(`stay.on.page`),
            color: 'blue--text',
            action: () => {
              this.closeDialog();
            },
          },
          {
            text: this.$t(`leave`),
            color: 'blue--text',
            action: () => {
              this.onCloseRegistration();
            },
          },
        ],
      };
      this.openDialog({
        payload,
        setting: { maxWidth: '18.5rem', closeOnClickOutside: true },
      });
    },    
    onCloseRegistration: function(event) {    
        this.profileModel.Username = '';
        this.profileModel.Password ='';
        this.profileModel.ConfirmPassword ='';
        this.profileModel.FirstName = '';
        this.profileModel.LastName = '';
        this.profileModel.Country = '';
        this.profileModel.Language = '';
        this.profileModel.Currency = '';
        this.profileModel.City = '';
        this.profileModel.Address = '';
        this.profileModel.Postal = '';
        this.profileModel.DialCode = '';
        this.profileModel.ContactNo = '';
        this.profileModel.Email = ''; 
        this.profileModel.ChatTool = '';
        this.profileModel.ChatAddress = '';
        this.profileModel.OwnWebsite = 0;
        this.profileModel.WebUrl = '';
        this.profileModel.Describe = '';
        this.profileModel.Captcha = '';
        this.profileModel.AffiliateCode = '';    
        this.e1 = 1;  
        this.$emit("onCloseRegistration");
    },  
    selectPayload(selectType) {       
      switch (selectType)
      {
        case 'lang':
          this.optionsPayload = Object.values(this.Languages).map((item) => {
            return {
              key: item.Code,
              text: item.Descr,
            };
          });  
          break;
        case 'Currency':
          this.optionsPayload = Object.values(this.Currencys.Currenies).map((item) => {
            return {
              key: item,
              text: item,
            };
          });  
          break;          
        case 'DialCode':
          this.optionsPayload = Object.values(this.DialCodes).map((item) => {
            return {
              key: item.Code,
              text: item.Descr,
            };
          });
          break;
        case 'ChatTool':
          this.optionsPayload = Object.values(this.ChatTools).map((item) => {
            return {
              key: item.Code,
              text: item.Descr,
            };
          });
          break;           
        case 'Country':
          this.optionsPayload = Object.values(this.Countrys).map((item) => {
            return {
              key: item.Code,
              text: item.LangValue,
            };
          });
          break;            
      }

      this.$emit('openSelect', this.optionsPayload, selectType);
    }, 
    getLanguageText(code) {     
      let _item = this.Languages.find((item) => item.Code == code);      
      if (_item === undefined) return '————';     
      return _item?.Descr;
    },  
    getCurrencyText(code) {     
      let _item = this.Currencys.Currenies.find((item) => item == code);      
      if (_item === undefined) return '————';     
      return _item;
    },            
    getDialCodeText(code) {
      let _item = this.DialCodes.find((item) => item.Code == code);
      if (_item === undefined) return '————';
      return _item?.Descr;
    }, 
    getChatToolText(code) {
      let _item = this.ChatTools.find((item) => item.Code == code);
      if (_item === undefined) return '————';
      return _item?.Descr;
    }, 
    getCountryText(code) {
      let _item = this.Countrys.find((item) => item.Code == code);
      if (_item === undefined) return '————';
      return _item?.LangValue;
    }, 
    async onRegister() {
    if (
        this.checkOs().isAndroid ||
        this.checkOs().isPhone ||
        this.checkOs().isTablet
      ) {
        this.PlatformId = 2
      } else if (this.checkOs().isPc) {
        this.PlatformId = 1
      }      
      const data = {
        Username: this.profileModel.Username,
        Password: this.profileModel.Password,      
        FirstName: this.profileModel.FirstName,
        LastName: this.profileModel.LastName,
        Country: this.profileModel.Country,
        Language: this.profileModel.Language,
        Currency: this.profileModel.Currency,
        City: this.profileModel.City,
        Address: this.profileModel.Address,   
        Postal: this.profileModel.Postal,
        DialCode: this.profileModel.DialCode,
        ContactNo: this.profileModel.ContactNo,
        Email: this.profileModel.Email, 
        ChatTool: this.profileModel.ChatTool,
        ChatAddress: this.profileModel.ChatAddress,
        OwnWebsite: this.profileModel.OwnWebsite,
        WebUrl: this.profileModel.WebUrl,
        Describe: this.profileModel.Describe,
        AffiliateCode: this.profileModel.AffiliateCode,
        Captcha: this.profileModel.Captcha,
        RegistrationPlatformId: this.PlatformId      
      };
      const response = await this.registerAccount(data);
      if(this.RegisterResponse.data.Status === 0)
      {
        this.onCloseRegistration();
        this.$router.push({ path: `/${this.langCode}/successpage` });
      }
    },
    onWebUrlBlur() {      
       if( this.profileModel.OwnWebsite === 0 && this.profileModel.WebUrl === '')
       {
          this.isValidateWebUrl = true;
       }
       else
       {
          this.isValidateWebUrl = false;
       }
    },
    onDescribeBlur() {      
       if( this.profileModel.OwnWebsite === 1 && this.profileModel.Describe === '')
       {
          this.isValidateWebUrl = true;
       }
       else
       {
          this.isValidateWebUrl = false;
       }
    },
    refresh(){
      this.versionNumber = new Date().getTime();
    },
    checkOs() {
      var ua = navigator.userAgent,
        isWindowsPhone = /(?:Windows Phone)/.test(ua),
        isSymbian = /(?:SymbianOS)/.test(ua) || isWindowsPhone,
        isAndroid = /(?:Android)/.test(ua),
        isFireFox = /(?:Firefox)/.test(ua),
        isChrome = /(?:Chrome|CriOS)/.test(ua),
        isTablet =
          /(?:iPad|PlayBook)/.test(ua) ||
          (isAndroid && !/(?:Mobile)/.test(ua)) ||
          (isFireFox && /(?:Tablet)/.test(ua)),
        isPhone = /(?:iPhone)/.test(ua) && !isTablet,
        isPc = !isPhone && !isAndroid && !isSymbian;
      return {
        isTablet: isTablet,
        isPhone: isPhone,
        isAndroid: isAndroid,
        isPc: isPc,
      };
    },                  
  },
};
</script>
